import Logger from "./logger";
import EventEmitter from "events";
import { IMessage } from "./linkServerService";
import { randomId } from "./utils";

export interface PeerConfiguration {
  rtcConfiguration?: RTCConfiguration,
  messageIdLength: number
}

export interface IPeerMessage<TypeType = string, PayloadType = any>
  extends IMessage<TypeType, PayloadType> {
  id: string
}

interface Peer {
  on(event: 'message', cb: (message: IPeerMessage, peer: Peer) => void): this;
  on(event: 'close', cb: (peer: Peer) => void): this;
}

class Peer extends EventEmitter {
  private readonly peerConfiguration: PeerConfiguration;
  private readonly peerConnection: RTCPeerConnection;
  private readonly receiverSocket: RTCDataChannel;
  private readonly transmitterSocket: RTCDataChannel;

  constructor(peerConnection: RTCPeerConnection,
              receiverSocket: RTCDataChannel,
              transmitterSocket: RTCDataChannel,
              peerConfiguration?: PeerConfiguration) {
    super();
    this.peerConnection = peerConnection;
    this.receiverSocket = receiverSocket;
    this.transmitterSocket = transmitterSocket;
    this.peerConfiguration = {
      messageIdLength: 8,
      ...peerConfiguration
    };

    this.peerConnection.addEventListener("connectionstatechange", this.connectionStateHandler.bind(this));
    this.receiverSocket.onmessage = (event) => this.emit('message', JSON.parse(event.data), this);
  }

  private connectionStateHandler() {
    switch (this.peerConnection.iceConnectionState) {
      case "failed":
      case "closed":
      case "disconnected":
        Logger.warn("Peer dsiconnected");
        this.peerConnection.close();
        this.emit('close', this);
        break;
      case "completed":
        Logger.log("Connection completed");
        break;
    }
  }

  send(type: string, payload: any) {
    this.transmitterSocket.send(JSON.stringify({ type, payload, id: randomId(this.peerConfiguration?.messageIdLength) }));
  }
}

export default Peer;