import EventEmitter from "events";
import LinkServerService, { LinkServerServiceConfig } from "./linkServerService";
import Logger, { LogLevel } from "./logger";
import Peer, { IPeerMessage } from "./peer";

export interface NetworkConfig {
  linkServerConfig?: LinkServerServiceConfig
  logLevel?: LogLevel,
  startPeersCount?: number
}

interface Network {
  on(event: 'connection', listener: (peer: Peer) => void): this;
  on(event: 'close', listener: (peer: Peer) => void): this;
};

class Network extends EventEmitter {
  private readonly startPeersCount: number;
  private readonly peers: Peer[] = []
  private linkServer: LinkServerService;

  get availablePeers() : ReadonlyArray<Peer> {
    return this.peers;
  } 

  constructor(networkConfig?: NetworkConfig) {
    super();
    const config = {
      linkServerConfig: {
        path: 'localhost',
        secure: false,
        port: 7070
      },
      logLevel: LogLevel.Disabled,
      startPeersCount: 3,
      ...networkConfig
    };

    Logger.logLevel = config.logLevel;
    this.startPeersCount = config.startPeersCount;
    this.linkServer = new LinkServerService(config.linkServerConfig);
    this.linkServer.on('open', this.start.bind(this));
    this.linkServer.on('connection', this.connectionReceived.bind(this))
  }

  private start() {
    this.linkServer.searchPeers(this.startPeersCount);
  }

  private connectionReceived(peer: Peer) {
    this.peers.push(peer);
    Logger.log(`Peer connected - ${this.peers.length}`);
    this.emit('connection', peer);
    peer.on('message', this.handleMessage.bind(this));
    peer.on('close', this.connectionClosed.bind(this));
  }

  private connectionClosed(peer: Peer) {
    const index = this.peers.indexOf(peer);
    this.peers.splice(index, 1);
    Logger.log(`${this.peers.length} peers connected`);
    this.emit('close', peer);
  }

  private handleMessage(message: IPeerMessage) {
    console.log(message)
  }
}

export default Network;